import 'uni-pages';
import Vue from 'vue'
import App from './App'
import urlRequest from './common/request.js'

Vue.config.productionTip = false

Vue.prototype.$baseUrl = 'https://api2.3733.com/api';
Vue.prototype.$uniRequest = function(url, param,func) {
			urlRequest(url,param,func)
		}

App.mpType = 'app'

const app = new Vue({
    ...App
})
app.$mount()